import app from './_firebase';

import {getFirestore} from 'firebase/firestore'

export const listLivestreamVideo = async () => {
  try {
    let querySnapshot = await app.firestore().collection('video').get();
    const content = querySnapshot.docs.map((doc) => {
      return {
        id: doc.id,
        ...doc.data()
      };
    });
    return content;
  } catch (err) {
    console.log(err);
  }
};


export const addLivestreamVideo = async (values) => {
  try {
    values.content_type = 'livestream';
    values.create_at = getFirestore().FieldValue.serverTimestamp();
    let contentRef = await app.firestore().collection('video').add(values);
    let content = await contentRef.get();
    return content.data();
  } catch (err) {
    console.log(err);
  }
};

export const deleteContents = (ids) => {
  try {
    ids.map((id) => {
      let cover_url = 'public/images/content/' + id;
      var storageRef = app.storage().ref();
      storageRef.child(cover_url).getDownloadURL().then(onResolve, onReject);

      function onResolve(foundURL) {
        storageRef.child(cover_url).delete();
      }

      function onReject(error) {
        console.log(error.code);
      }

      app.firestore().collection('contents').doc(id).collection('detail').doc('content').delete();
      app.firestore().collection('contents').doc(id).delete();
    });
  } catch (err) {
    console.log(err);
  }
};

